.Topbar {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}

__dates.Topbar, .Topbar header {
  font-weight: 500;
}

.Topbar__nav a.--active {
  font-weight: 500;
  text-decoration: underline;
}

@media (min-width: 741px) {
  .Topbar {
    --gap: 24px;
    justify-content: center;
    align-items: center;
    gap: 0 var(--gap);
    padding: 0 max(32px, (100vw - var(--max-w)) / 2);
    flex-wrap: wrap;
    font-size: 20px;
    display: flex;
  }

  .Topbar > * {
    height: 48px;
    white-space: nowrap;
    align-items: center;
    display: flex;
  }

  .Topbar__dates {
    white-space: break-spaces;
  }

  nav {
    gap: var(--gap);
  }

  .Topbar__langs {
    gap: calc(var(--gap) / 2);
  }

  .Topbar__langs > * {
    cursor: pointer;
    display: inline-block;
  }
}

html[lang="ge"] .Topbar__langs [data-value="ge"], html[lang="en"] .Topbar__langs [data-value="en"] {
  font-weight: 500;
}

@media (max-width: 740px) {
  .Topbar {
    --gap: 24px;
    justify-content: space-between;
    gap: 0 var(--gap);
    flex-wrap: wrap;
    padding: 8px 24px;
    font-size: 16px;
    display: flex;
  }

  .Topbar > * {
    height: 48px;
    white-space: nowrap;
    align-items: center;
    display: flex;
  }

  .Topbar__header, .Topbar__dates {
    font-weight: 500;
  }

  .Topbar__dates {
    white-space: initial;
    flex-direction: column;
    justify-content: center;
    align-items: flex-end;
    gap: 2px 0;
  }

  nav {
    gap: var(--gap);
  }

  .Topbar__langs {
    gap: calc(var(--gap) / 2);
  }

  .Topbar__langs > * {
    cursor: pointer;
    display: inline-block;
  }

  .Topbar__langs > *.--active {
    font-weight: 500;
  }
}

